footer {
  background-image: url(./../../img/19.png), url(./../../img/back-game.png);
  background-repeat: no-repeat, repeat;
  background-size: 100%, cover;
  background-position: bottom, left;
  border-top: 3px solid #79710b;
  min-height: 100vh;
  text-align: center;
  padding-top: 1rem;
  position: relative;
}

.footerfirstarea {
  display: table;
  width: 100%;
}
.footerfirsttitle {
  margin-bottom: 50px;
}
.footerfirsttitle h1 {
  text-shadow: 0 0 50px yellow;
}
.footerimgsdiv {
  display: flex;
  flex-direction: row;
  margin: 30px;
}
.footerimgs {
  flex: 33.3%;
  line-height: 0.5rem;
}

.footerimgs img {
  width: 90%;
}
.footerimgs p {
  font-size: 30px;
  margin: 0.51rem 0 0.3rem 0;
}

.footerimgs span {
  font-size: clamp(0.51rem, 16px, 0.8rem);
  padding: 0 5px;
}

@media (max-width: 900px) {
  .footerimgsdiv {
    flex-direction: column;
  }
  .footerimgs img {
    width: 300px;
  }
}
@media (max-width: 500px) {
  .footerimgs img {
    width: 80%;
    margin-top: 20px;
  }
  .footerfirsttitle {
    margin-bottom: 0;
  }
}

.footersecondarea {
  display: table;
  width: 100%;
}

.footersecondchilddivs {
  float: left;
  width: 50%;
  border-top: 1px solid #a79d14;
  border-bottom: 1px solid #a79d14;
  border-right: 1px solid #a79d14;
  padding: 50px;
}
@media (max-width: 400px) {
  .footersecondchilddivs {
    padding: 50px 0;
  }
}

.footersecondchilddivs p {
  font-size: 50px;
  margin-bottom: 10px;
  text-shadow: 0 0 50px yellow;
}

.footersecondchilddivs img {
  width: 90%;
}

@media (max-width: 900px) {
  .footersecondchilddivs {
    width: 100%;
  }
}
.footerthirdarea {
  padding: 50px;
  text-align: center;
  border-bottom: 2px solid rgb(161, 161, 161);
}
.footerthirdimgs1 {
  display: flex;
  flex-direction: row;
  margin: auto;
}
.footerthirdimgs1 a {
  flex: 25%;
}
.footerthirdimgs2 {
  display: table;
  margin: auto;
}

.footerthirdimgs2 img {
  margin: 30px 20px 0;
}

@media (max-width: 1200px) {
  .footerthirdimgs1 {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 500px) {
  .footerthirdimgs1 .thirdimg {
    width: 100%;
  }
}
@media (max-width: 400px) {
  .footerthirdimgs1 .thirdimg {
    width: 100%;
  }
}

.social-media-area ul {
  display: flex;
}
.sponsors {
  display: flex;
  grid-gap: 1.5rem;
  padding: 0 1rem;
}
.sponsors img {
  max-height: 181px;
  height: auto;
  width: 100%;
}
.border-bottom {
  border-bottom: 1px solid gray;
  width: 100%;
  display: grid;
  place-items: center;
}
.legal {
  font-size: 12px;
  padding: 0.81rem 0;
  color: gray;
  & a {
    color: gray;
    &:hover {
      color: white;
    }
  }
}
.footer-links {
  font-size: 2vw;
  margin: 1rem;
  > div > * {
    margin: 1rem;
  }
}
